import "./projects.scss";
import React, { useContext } from "react";
import { ContextApp } from "../../context/contextApp";
import CardProject from "../../components/cardProject/cardProject";

//import images
import project_01 from "../../assets/mini_01.png";
import project_02 from "../../assets/mini_02.png";
import project_03 from "../../assets/mini_03.png";
import project_04 from "../../assets/mini_04.png";

export default function Projects() {
  const { darkState } = useContext(ContextApp);
  const darkModeTxt = darkState ? "darkTxtProject" : "lightTxtProject";
  const projects = [
    {
      id: '01',
      title: 'Reward Store',
      img: project_01,
      gitHub: 'https://github.com/iDeerBoy/reward-store-jimmy-pulido',
      url: 'https://ideerboy.github.io/reward-store-jimmy-pulido/#/',
      color: '#FFA5A5'
    },
    {
      id: '02',
      title: 'Gifos',
      img: project_02,
      gitHub: 'https://github.com/iDeerBoy/Poyecto02-Gyphos',
      url: 'https://ideerboy.github.io/Poyecto02-Gyphos/',
      color: '#4A1EE3'
    },
    {
      id: '03',
      title: 'Easy sleep',
      img: project_03,
      gitHub: 'https://github.com/iDeerBoy/easy-sleep',
      url: 'https://ideerboy.github.io/easy-sleep/',
      color: '#654062'
    },
    {
      id: '04',
      title: 'Podcast Channel',
      img: project_04,
      gitHub: 'https://github.com/iDeerBoy/Podcast-Channel',
      url: 'https://ideerboy.github.io/Podcast-Channel/',
      color: '#0000E2'
    }
  ]

  return (
    <section className="projectsContainer">
      <div className={`txtProjectsContainer ${darkModeTxt}`}>
        <h2>My skills</h2>
        <p>
          An e-commerce app that uses a point system reward to purchase
          household elements. It implements the most recent tools of the
          Framework like hooks as useContext and useEffect.
        </p>
      </div>
      <div className='cardContainer'>
        {projects.map( (i) => <CardProject {...i} key={i.id} /> )}
      </div>
    </section>
  );
}
