import "./cardProject.scss";
import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlay } from "@fortawesome/free-solid-svg-icons";
import gitHubLogo from "../../assets/github-square-brands.svg";

export default function CardProject({ title, img, gitHub, url, color }) {
  const [linksState, setLinksState] = useState(false);

  return (
    <div
      className="cardProject"
      style={{ backgroundColor: color }}
      onMouseEnter={() => setLinksState(true)}
      onMouseLeave={() => setLinksState(false)}
    >
      <h4>{title}</h4>
      <img src={img} alt={title} />
      <div className="linksMobile" style={{ backgroundColor: color }}>
        <a href={gitHub} target="_blank" rel="noreferrer">
          <img src={gitHubLogo} alt="github" />
        </a>
        <a href={url} target="_blank" rel="noreferrer">
          <FontAwesomeIcon icon={faPlay} />
        </a>
      </div>
      {linksState && (
        <div className="linksDesktop" style={{ backgroundColor: color }}>
          <a href={gitHub} target="_blank" rel="noreferrer">
            <img src={gitHubLogo} alt="github" />
          </a>
          <a href={url} target="_blank" rel="noreferrer">
            <FontAwesomeIcon icon={faPlay} />
          </a>
        </div>
      )}
    </div>
  );
}
